<template>
  <!--账号密码登录-->
  <div>
    <a-form-model ref="formLogin" :rules="rules" :model="formLogin" @submit="login" @submit.native.prevent>
      <a-form-model-item prop="username">
        <a-input v-model="formLogin.username" placeholder="请输入用户名" size="large">
          <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)"/>
        </a-input>
      </a-form-model-item>
      <a-form-model-item prop="password">
        <a-input v-model="formLogin.password" type="password" placeholder="请输入密码" size="large">
          <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
        </a-input>
      </a-form-model-item>
      <a-form-model-item>
        <a-button
            type="primary" block
            html-type="submit"
            size="large"
            :loading="loading"
        >
          登 录
        </a-button>
      </a-form-model-item>
    </a-form-model>
    <div class="row remember-me">
      <div class="col">
        <router-link :to="{name: 'register'}" target="_blank">立即注册</router-link>
      </div>
      <div class="col text-end">
        <router-link :to="{name: 'getPass'}" target="_blank">忘记密码</router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import request from "@/utils/request";
  import storage from "store";

  export default {
    name: 'LoginAccount',
    data() {
      return {
        formLogin: {
          username: '',
          password: '',
        },
        loading: false,
        rules: {
          username: [{required: true, message: '请输入用户名！', trigger: 'blur'}],
          password: [{required: true, message: '请输入密码！', trigger: 'blur'}]
        }
      };
    },
    methods: {
      login() {
        this.$refs.formLogin.validate(valid => {
          if (!valid) {
            this.$message.error('请先规范填写表单！');
            return false;
          }
          
          this.loading = true;
          const {username, password} = this.formLogin;

          request({
            method: 'post',
            url: '/ouser/before/login',
            data: {username: username, password: password}
          }).then(res => {
            if (res.code === 1) {
              const token = res.data;
              storage.set('token', token);
              this.$message.success('登录成功！');
              this.$router.push({name: 'home'});
            } else {
              this.$message.error(res.msg);
              this.formLogin = {username: '', password: ''};
            }
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        })
      },
    },
    activated() {
      this.loading = false;
      this.formLogin = {username: '', password: ''};
    }
  }
</script>

<style scoped>

  .ant-form {
    margin-top: 5%;
  }

  /deep/ .ant-input-lg {
    height: 50px;
  }

  .ant-btn-lg {
    height: 48px;
  }

  .checkbox {
    margin-top: -5%;
    margin-bottom: -6%;
  }


  .form-group {
    margin-bottom: 1rem;
  }

  .remember-me {
    font-size: 0.9rem;
    color: gray;
    margin-bottom: -6.3%;
  }

  a {
    color: grey;
  }

  a:hover {
    text-decoration: underline grey;
  }
</style>